import uniPopup from "../uni-popup/uni-popup.vue"
import uniNavBar from "../uni-nav-bar/uni-nav-bar.vue"
import xwEmpty from "../xw-empty/xw-empty.vue"
import uniSearchBar from '../uni-search-bar/uni-search-bar.vue'
import uniLoadMore from "../uni-load-more/uni-load-more.vue"
export default {
	name: 'zhilin-select',
	components: {
		uniPopup,
		uniNavBar,
		xwEmpty,
		uniSearchBar,
		uniLoadMore
	},
	props: {
		title: {
			type: String,
			default: '请选择'
		},
		value: {
			type: Boolean,
			required: true
		},
		data: {
			type: Array,
			required: true
		},
		multiple: {
			type: Boolean,
			default: true
		},
		showSearch: {
			type: Boolean,
			default: false
		},
		searchInput: Function,
		initSelected: Array
	},
	data() {
		return {
			list: [],
			selected: [],
			showBottom: false,
			loading: false,
			searchVal: ''
		}
	},
	created() {
		this.dataInit()
	},
	watch: {
		value(n, o) {
			if (n) this.$refs.popup.open()
			else this.$refs.popup.close()
		},
		data(n, o) {
			this.loading = false
			this.showBottom = false
			this.dataInit()
		},
		searchVal(){
			this.inputSearch()
		},
		initSelected(n){
			this.selected = n
		}
	},
	methods: {
		lower: function(e) {
			this.showBottom = true
		},
		dataInit() {
			if (typeof this.data[0] == "string") {
				this.list = this.data.map((v, i) => ({
					label: v,
					value: v
				}))
			} else {
				this.list = this.data
			}
		},
		clickLeft() {
			this.$emit("input", false)
		},
		tapItem(val) {
			if (this.multiple) {
				let idx = this.selected.indexOf(val)
				if (idx == -1) {
					this.selected.push(val)
				} else {
					this.selected.splice(idx, 1)
				}
			} else {
				this.selected = [val]
			}
			this.$emit("change", this.selected.join())
		},
		tapOK() {
			this.$emit("input", false)
			this.$emit("confirm", this.selected.join())
		},
		popupChange(e) {
			this.$emit("input", e.show)
		},
		inputSearch() {
			let val = this.searchVal
			if(this.searchInput){
				this.loading = true
				this.searchInput(val)
				return
			}
			this.showBottom = false
			if(typeof this.data[0] == 'string'){
				this.list = this.data.filter(v=> v.indexOf(val)>-1).map((v, i) => ({
					label: v,
					value: v
				}))
			}else{
				this.list = this.data.filter(v=> v.label.indexOf(val)>-1)
			}
		}
	}
}
